body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #7c50d9;
  text-align: center;
  color: #fff;
}
/*claro #83bfae
borde #9ecdc0*/

header {
  background-image: url("pattern.png");
  padding: 15px 0 5px 0;
  -webkit-box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
          box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}

header h2 {
  font-size: 34px;
  margin: 0;
}

header p {
  font-size: 12px;
  margin: 10px 0 0 0;
}

button {
  margin: 0 2px;
}

.addRecipeBtn {
  margin: 20px auto;
}

/* Dismiss button */
.delete-button {
  float: right;
  position: relative;
  bottom: 47px;
  border: 0;
  color: #fff;
  font-size: 24px;
  line-height: 20px;
  padding: 0;
  background-color: #8A5EE7;
}

.delete-button:hover {
  background-color: transparent;
  color: #f84949;
  text-shadow: 0 0 2px rgba(255, 255, 255, 0.9), 0 0 5px rgba(255, 255, 255, 0.5);
}

.panel-group {
  margin-bottom: 10px; /* Space between panels */
}

.panel-group .panel {
  border-radius: 8px;
  background-color: #8a5ee7;
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}

.panel-default {
  border-color: #8a5ee7;
  border-top-color: #9065ea;
}

.panel-default > .panel-heading {
  background-color: #8a5ee7;
  border-color: #8a5ee7;
  border-radius: 8px;
  color: #fff;
}

.panel-heading:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

.panel-heading:hover ~ .panel-collapse .delete-button {
  background-color: rgba(0, 0, 0, 0.001);
}

.panel-title a {
  display: block;
  padding: 10px 15px;
  margin: -10px -15px;
  text-decoration: none;
  cursor: pointer;
  font-weight: bold;
}

.panel-default > .panel-heading + .panel-collapse > .panel-body {
  border-top-color: #936fdf;
}

.alert {
  margin: 0;
  padding: 5px;
}

.ingredients {
  text-align: left;
}



